t:6px;margin-top:16px}.app-main__navigation__item+.app-main__navigation__item{margin-left:35px}.app-main__content{margin-top:37px;width:100%}.app-main__content__video{height:274px}.app-main__content__video__title{top:21px;left:21px}.app-main__content__video__sub{top:51px;left:21px}.app-main__content__video__duration{top:21px;right:42px}.app-main__content__video__play-logo{bottom:41px;left:19px;width:58px;height:54px}.app-main__content__video+.app-main__content__video{margin-top:64px}}@media screen and (max-width: 480px){.app-main__head h1{font-size:24px}.app-main__navigation{margin-top:6px}.app-main__navigation__item{font-size:16px}.app-main__navigation__item+.app-main__navigation__item{margin-left:20px}.app-main__content{margin-top:27px;width:100%}.app-main__content__video{height:174px}.app-main__content__video__title{top:21px;left:21px;font-size:16px}.app-main__content__video__sub{top:51px;left:21px;font-size:14px}.app-main__content__video__duration{top:21px;right:42px;font-size:16px}.app-main__content__video__play-logo{bottom:21px;left:19px;width:28px;height:24px}.app-main__content__video+.app-main__content__video{margin-top:64px}}.app-aside{background:#FFE4D6;width:100%;max-width:160px;box-sizing:border-box;padding:127px 0}.app-aside__nav{display:flex;justify-content:center;align-items:center;width:100%;margin-bottom:72px;border-left:3px solid rgba(0,0,0,0);transition:.3s linear}.app-aside__nav svg{width:48px;cursor:pointer;fill:#E1AA98;transition:.3s linear}.app-aside__nav_active,.app-aside__nav:hover{border-left:3px solid #8964D7}.app-aside__nav_active svg,.app-aside__nav:hover svg{fill:#8964D7}@media screen and (max-width: 1224px){.app-aside{max-width:100px}}@media screen and (max-width: 480px){.app-aside{max-width:50px}.app-aside__nav{margin-bottom:32px}.app-aside__nav svg{width:18px}}

